/* Стилі для маленьких екранів (наприклад, мобільних телефонів) */
@media (max-width: 600px) {
    .user-reply, .site-reply {
      padding: 8px 16px;
      border-radius: 10px;
      margin-bottom: 8px;
      font-size: 14px;
    }
    
    .title {
      font-size: 20px;
    }
    
    .subtitle {
      font-size: 16px;
    }
  }
  
  /* Стилі для середніх екранів (наприклад, планшетів) */
  @media (min-width: 601px) and (max-width: 1024px) {
    .user-reply, .site-reply {
      padding: 10px 20px;
      border-radius: 15px;
      margin-bottom: 10px;
      font-size: 16px;
    }
    
    .title {
      font-size: 24px;
    }
    
    .subtitle {
      font-size: 18px;
    }
  }
  
  /* Стилі для великих екранів (наприклад, настільних комп'ютерів) */
  @media (min-width: 1025px) {
    .user-reply, .site-reply {
      padding: 12px 24px;
      border-radius: 20px;
      margin-bottom: 12px;
      font-size: 18px;
    }
    
    .title {
      font-size: 28px;
    }
    
    .subtitle {
      font-size: 20px;
    }
  }
  